import React, { PropTypes, Component  } from 'react';
import KlassName from 'teapan/klassName';
import { SIZES, ALIGNS } from '../../constant/props';

class DropdownContent extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			className,
			children,
			size,
			align,
			...others
		} = props;

		const klassName = KlassName(
			'dropdown-content',
			{
				['dropdown-content--' + size]:size,
				['dropdown-content--' + align]:align,
			},
			className
		);

		return (
			<div {...others} className={klassName}>
				{children}
			</div>
		);
	}
};

DropdownContent.propTypes = {
	activated: PropTypes.bool,
	className: PropTypes.string,

	size: PropTypes.oneOf(SIZES),
	align: PropTypes.oneOf(ALIGNS),

	children: PropTypes.any,
};

DropdownContent.defaultProps = {

};

export default DropdownContent;